/**
 * Debug endpoint to check metrics freshness
 * GET /api/debug/metrics-status
 */

import { NextResponse } from 'next/server';
import { loadAllSummaries } from '@/services/facilitator-metrics-service';
import { createLogger } from '@/lib/logger';

const logger = createLogger({ component: 'MetricsStatusDebug' });

export const dynamic = 'force-dynamic';

export async function GET() {
  try {
    const summaries = await loadAllSummaries();
    const now = Date.now();
    
    const status = Object.entries(summaries).map(([key, summary]) => {
      const fetchedAt = new Date(summary.fetchedAt);
      const ageHours = (now - fetchedAt.getTime()) / (1000 * 60 * 60);
      
      return {
        key,
        facilitatorId: summary.facilitatorId,
        timeframe: summary.timeframe,
        fetchedAt: summary.fetchedAt,
        ageHours: Math.round(ageHours * 10) / 10,
        isStale: ageHours > 4,
        totalInvocations: summary.totalInvocations,
        dataStart: summary.dataStart,
        dataEnd: summary.dataEnd,
      };
    });
    
    // Sort by age (most stale first)
    status.sort((a, b) => b.ageHours - a.ageHours);
    
    const allStale = status.every(s => s.isStale);
    const oldestAge = status[0]?.ageHours || 0;
    
    logger.info({
      summaryCount: status.length,
      allStale,
      oldestAgeHours: oldestAge,
      msg: 'Metrics status check',
    });
    
    return NextResponse.json({
      ok: true,
      timestamp: new Date().toISOString(),
      summary: {
        totalFacilitators: status.length,
        allDataStale: allStale,
        oldestDataHours: oldestAge,
        recommendation: allStale 
          ? 'Run pull-metrics job to refresh data'
          : 'Data is fresh',
      },
      facilitators: status,
    });
  } catch (error: any) {
    logger.error({ error: error.message, msg: 'Failed to check metrics status' });
    return NextResponse.json({
      ok: false,
      error: error.message,
    }, { status: 500 });
  }
}

